@import url(https://fonts.googleapis.com/css?family=Nunito:400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rajdhani:300&display=swap);
body{
    background-color: rgb(218, 218, 218) !important;
    font-family: 'Nunito', sans-serif !important;

}

.navbar{
    background-color: rgb(27, 27, 27);
    font-family: 'Rajdhani', sans-serif;
}

.nav-item h3{
margin: 0 30px 0 30px;

}

.inactive{
    color: white;
}

.inactive:hover{
    color: rgb(94, 93, 93);
}


.active{

    background: -webkit-linear-gradient(rgb(42, 180, 190), rgb(101, 8, 223));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}



/* ******** Responsive Styles *********/
@media (max-width: 767px){
   .nav{
        display: block !important;
    }

    .navbar h3{
        font-size: 20px;
    }



}


@media (max-width: 575.98px){
    .navbar h3{
         font-size: 15px;
     }
 
 }

#home-container{
    text-align: center;
    margin: 3% 5% auto 5%;
    line-height: 25px;
}
.card-size{
    width: 200px;
    height: 100%;
    border-radius: 10px; 
}

.card-container{
    margin: 15px;
    position: relative;

}

.card-overlay{
    position: absolute;
    width: 200px;
    height: 100%;
    background: rgba(0, 0, 0, 0.774);
    font-size: 12px;
    color: white;
    overflow: scroll;
    border-radius: 10px;
    padding: 10px;
    opacity: 0;

}

.card-overlay:hover{
    opacity: 1;  
    -webkit-transition: opacity 0.1s ease-in-out;  
    transition: opacity 0.1s ease-in-out; 
}


.card-title{
    margin-bottom: 5px !important;
    font-size: 12.4px;
    font-weight: bolder;
  
}







/***** Responsive Styles ****/


/* Medium devices (tablets, less than 992px)*/
@media (max-width: 991.98px){
    .card-size{
        width: 160px;
        height: 100%;
        border-radius: 10px; 
    }

    .card-overlay{
        width: 160px;
    }

}

 /* Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
    .card-size{
        width: 120px;
        height: 100%;
        border-radius: 10px; 
    }

    .card-overlay{
        width: 120px;
    }
  
}

 /* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) { 
    .card-size{
        width: 80px;
        height: 100%;
        border-radius: 10px; 
    }

    .card-overlay{
        width: 80px;
    }

    .card-container{
        margin: 8px;    
    }
    
}
#posters-container{
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    margin-bottom: 20px;
   
}


.loader{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader-img{
    width: 40px;
    height: 40px;    
}


